.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #c2c2d6;
}

#calculator {
    position: relative;
    width: 320px;
    border: 2px solid #47476b;
    background-color: black;
    padding: 5px;
}

.formulaScreen {
    min-height: 20px;
    text-align: right;
    font-size: 20px;
    /* font-family: 'LED Digital 7', sans-serif;                                                */
    line-height: 20px;
    color: orange;
}

.outputScreen {
    font-size: 29px;
    line-height: 35px;
    /* font-family: 'LED Digital 7', sans-serif;                                                */
    color: white;
    text-align: right;
}

button {
    height: 65px;
    width: 80px;
    color: white;
    border: none;
    outline: 1px solid black;
    font-size: 20px;
    cursor: default;
    background-color: #4d4d4d;
}

button:hover {
    color: black;
    outline: 0.05px solid grey;
}

/* jumbo button */
#zero, #clear {
    width: 160px;
}

#clear {
    background-color: #ac3939;
}

/* operation */
#add, #subtract, #multiply, #divide {
    background-color: #666666;
}

#equals {
    position: absolute;
    bottom: 5px;
    background-color: #004466;
    height: 130px;
}